html {
  overflow-x: hidden;
}

body {
  transition: padding-top 0.3s ease-in-out;
  background-color: #fff;
  color: #666;
}

a {
  transition: color 0.3s;
  color: #1da0c1;
}

a:hover, a:focus {
  color: #5dcbe7;
}

a:visited {
  color: #1da0c1;
}

a:visited:hover, a:visited:focus {
  color: #5dcbe7;
}

.main-container {
  min-height: 80vh;
  background-color: #eee;
}

.main-container main {
  background-color: #fff;
}

.footer-container {
  min-height: 20vh;
  background-color: #1da0c1;
}

.footer-container footer {
  background-color: inherit;
  color: #fff;
}

.masonry {
  column-count: 1;
  column-gap: 0;
  counter-reset: item-counter;
}

@media screen and (min-width: 400px) {
  .masonry {
    column-count: 2;
  }
}

@media screen and (min-width: 800px) {
  .masonry {
    column-count: 3;
  }
}

@media screen and (min-width: 1200px) {
  .masonry {
    column-count: 4;
  }
}

.item {
  box-sizing: border-box;
  break-inside: avoid;
  padding: 10px;
}

.item:hover {
  cursor: pointer;
}

.item__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 310px;
  box-sizing: border-box;
}

.item__content:before {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 13px;
  width: 2em;
  height: 2em;
  line-height: 2em;
  text-align: center;
  font-weight: bold;
}

.item__content--small {
  height: 240px;
}

.item__content--medium {
  height: 270px;
}

.item__content--large {
  height: 350px;
}

.item .img-container {
  width: 100%;
  height: 70%;
  box-shadow: 0 8px 6px -6px #999;
}

.item .img-container img {
  width: 100%;
  height: 100%;
}

.item.no-info .img-container {
  height: 100%;
}

.item .img-info {
  box-sizing: border-box;
  width: 100%;
  height: 30%;
  padding: 5px 10px;
  background-color: #fafafa;
  box-shadow: 0 8px 6px -6px #999;
  overflow: hidden;
}

.item .img-info p {
  margin-bottom: 0;
  margin-top: 8px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.item .img-info p:first-child {
  margin-top: 0;
  text-align: center;
  font-weight: 500;
}

.item.no-info .img-info {
  display: none;
}

form {
